<script setup>
import { ref, onMounted } from 'vue'

const index = ref(0)

const shuffleArray = array => {
	for (let i = array.length - 1; i > 0; i--) {
		const j = Math.floor(Math.random() * (i + 1))
		;[array[i], array[j]] = [array[j], array[i]]
	}
}

const data = ref([
	{
		question: 'Организация любой правовой формы может привлечь к работе ВКС',
		answers: [
			'Да, ограничений нет',
			'Нет, я точно уверен(а) - есть ограничения по правовой форме организации',
		],
		correctAnswer:
			'Нет, я точно уверен(а) - есть ограничения по правовой форме организации',
		comments: [
			'Вы не правы. Требования установлены в п. 5 ст. 13.2 ФЗ от 25.07.2002 № 115-ФЗ. Например, привлекать ВКС вправе работодатели (или заказчики услуг), которые являются: российскими коммерческими организациями, научными, или образовательными организациями',
			'Вы правы! Например, индивидуальный предприниматель не является коммерческой организацией, на основании ст. 50 ГК. ',
		],
		isCorrect: null,
		selectedAnswerIndex: null,
	},
	{
		question:
			'Любого ли иностранного гражданина можно принять на работу, в статусе высококвалифицированного специалиста?',
		answers: [
			'Да. Все иностранные граждане могут работать как ВКС',
			'Нет, статус ВКС есть не у всех иностранцев',
		],
		correctAnswer: 'Нет, статус ВКС есть не у всех иностранцев',
		comments: [
			'Вы не правы. Нельзя принять на работу , например студента, беженца со статусом ВКС.',
			'Вы правы! Высококвалифицированным специалистом считается иностранный гражданин, имеющий большой опыт работы и уникальные навыки в какой либо  области трудовой деятельности.',
		],
		isCorrect: null,
		selectedAnswerIndex: null,
	},
	{
		question: 'Как ВКС подтвердить свой статус?',
		answers: [
			'Достаточно устно сообщить будущему работодателю',
			'Самостоятельно заявить о себе, как специалисте',
		],
		correctAnswer: 'Самостоятельно заявить о себе, как специалисте',
		comments: [
			'Вы не правы. Работодатель может проверить достоверность информации и пригласить на работу будущего сотрудника, на сайте МВД. Для этого специалисту нужно предварительно «заявить о себе» – самостоятельно подать свои данные в уполномоченные организации.',
			'Вы правы! Иностранный гражданин может сам заявить о себе, как ВКС, с предоставлением данных об опыте работы, навыков, или своих достижений. Для этого нужно обратиться в представительство МВД своего государства, консульское учреждение, или дипломатическое представительство.',
		],
		isCorrect: null,
		selectedAnswerIndex: null,
	},
	{
		question: 'Когда ВКС получает рабочую визу?',
		answers: [
			'После размещения иностранцем информации на сайте МВД',
			'Когда работодатель подаст ходатайство',
		],
		correctAnswer: 'Когда работодатель подаст ходатайство',
		comments: [
			'Вы не правы. Для проведения переговоров с будущим работодателем иностранному гражданину оформляется обыкновенная деловая виза сроком на 30 суток.',
			'Вы правы! Высококвалифицированным специалистом считается иностранный гражданин, имеющий большой опыт работы и уникальные навыки в какой либо  области трудовой деятельности.',
		],
		isCorrect: null,
		selectedAnswerIndex: null,
	},
	{
		question:
			'Какой категории иностранца - ВКС установлена заработная плата в размере 750 тыс. руб',
		answers: ['Абсолютно всем иностранцам', 'Не всем иностранцам ВКС'],
		correctAnswer: 'Не всем иностранцам ВКС',
		comments: [
			'Вы не правы. В пункте 3 ст. 13.2 ФЗ от 25.07.2002 № 115-ФЗ категория иностранцев отображена как: «в размере не менее семисот пятидесяти тысяч рублей из расчета за один квартал - для иных иностранных граждан».',
			'Вы правы! Например, для научных работников лимит заработной платы ниже. А для работников, которых например, привлекли к реализации проекта «Сколково» лимит заработной платы не установлен на законодательном уровне.',
		],
		isCorrect: null,
		selectedAnswerIndex: null,
	},
	{
		question:
			'Организация обязана уведомлять МВД о факте выплаты заработной платы. Какие сведения нужно подавать и как заполнить бланк?',
		answers: [
			'Только данные по должностному окладу. Бланк уведомления заполняется в произвольной форме.',
			'Нужно указать все вознаграждения сотрудника, включая надбавки и премии. Бланк уведомления утвержден.',
		],
		correctAnswer:
			'Нужно указать все вознаграждения сотрудника, включая надбавки и премии. Бланк уведомления утвержден.',
		comments: [
			'Вы не правы. Нужно указать полное вознаграждение за труд. Включая, например, компенсационные и стимулирующие выплаты, премии, или доплату за вредные условия труда.',
			'Нужно указать все вознаграждения сотрудника, включая надбавки и премии. Бланк уведомления утвержден.',
		],
		isCorrect: null,
		selectedAnswerIndex: null,
	},
])

const handleAnswerClick = (question, answerIndex) => {
	question.selectedAnswerIndex = answerIndex
	question.isCorrect = question.answers[answerIndex] === question.correctAnswer
}

onMounted(() => {
	data.value.forEach(question => {
		shuffleArray(question.answers)
	})
})

function increment() {
	if (index.value < data.value.length - 1) index.value += 1
}

function decrement() {
	if (index.value > 0) index.value -= 1
}

function reload() {
	location.reload()
}
</script>

<template>
	<div class="container">
		<!-- Показываем только текущий вопрос по индексу -->
		<div v-if="data.length > 0" class="container__question">
			<div class="question">{{ data[index].question }}</div>

			<div class="container__question-answers">
				<div
					v-for="(answer, answerIndex) in data[index].answers"
					:key="answerIndex"
					class="answer"
					:class="{ active: data[index].selectedAnswerIndex === answerIndex }"
					@click="handleAnswerClick(data[index], answerIndex)"
				>
					{{ answer }}
				</div>
			</div>

			<div class="container__question-comment">
				<Transition name="accordion">
					<div v-if="data[index].isCorrect !== null" class="comment">
						<div class="comment-head">Комментарий к ответу</div>
						<Transition mode="out-in">
							<span style="color: #0cda4f" v-if="data[index].isCorrect">
								{{ data[index].comments[1] }}
							</span>
							<span style="color: red" v-else>
								{{ data[index].comments[0] }}
							</span>
						</Transition>
					</div>
				</Transition>
			</div>
			<div class="line"></div>
		</div>
	</div>

	<div class="container__nav">
		<div v-if="index > 0" class="decrement">
			<img @click="decrement" src="../assets/back.svg" alt="" />
		</div>
		<div
			v-if="index < data.length - 1 && data[index].isCorrect !== null"
			class="increment"
		>
			<img @click="increment" src="../assets/forward.svg" alt="" />
		</div>
	</div>

	<Transition>
		<div
			class="container__end"
			v-if="index === data.length - 1 && data[index].isCorrect !== null"
		>
			<div class="end_msg">
				ВЫ ОТЛИЧНО СПРАВИЛИСЬ С ЗАДАНИЕМ! ЧТОБЫ ПРОЙТИ ТРЕНИРОВКУ ЕЩЕ РАЗ
				НАЖМИТЕ КНОПКУ ПРОЙТИ СНОВА
			</div>
			<div @click="reload" class="end_btn">ПРОЙТИ СНОВА</div>
		</div>
	</Transition>
</template>
<style scoped>
.container {
	margin: 0 1vw;
}

.container__question {
	margin: 1.5vw 0;
	display: flex;
	flex-flow: column wrap;
	justify-content: center;
	align-items: center;
}

.question {
	text-align: center;
	font-weight: 900;
}

.container__question-answers {
	margin: 1.5vw 0;
	display: grid;
	grid-template-columns: 1fr 1fr;
	gap: 10vw;
	justify-content: center;
	align-self: center;
}

.answer {
	width: 30vw;
	padding: 1.2vw;
	background-color: #f8cbac;
	border-radius: 2vw;
	display: flex;
	justify-content: center;
	align-items: center;
	cursor: pointer;
	text-align: center;
}

.container__question-comment {
	display: flex;
	justify-content: center;
}

.comment {
	max-width: 80vw;
	padding: 1vw;
	background-color: #deedff;
	border-radius: 1.5vw;
}

.comment-head {
	color: #3891fe;
	font-weight: 900;
}

.line {
	margin: 1vw 0;
	width: 90vw;
	height: 0.2vw;
	background-color: black;
}

.active {
	background-color: #4db0f7;
	color: #fafafa;
	transition: background-color 0.5s, color 0.5s;
}

.v-enter-active,
.v-leave-active {
	transition: opacity 0.5s ease;
	transition-delay: 3s;
}

.v-enter-from,
.v-leave-to {
	opacity: 0;
}

.accordion-enter-active,
.accordion-leave-active {
	transition: transform 0.3s ease-out, opacity 0.3s;
}

.accordion-enter-from,
.accordion-leave-to {
	transform: scaleY(0);
	opacity: 0;
}

.accordion-enter-to,
.accordion-leave-from {
	transform: scaleY(1);
	opacity: 1;
}

.container__nav img {
	width: 4vw;
	cursor: pointer;
	transition: scale 0.5s;
}

.container__nav img:hover {
	scale: 1.1;
}

.decrement {
	position: absolute;
	top: 12vw;
}

.increment {
	position: absolute;
	top: 12vw;
	right: 0;
}

.container__end {
	display: flex;
	flex-flow: column wrap;
	align-items: center;
	justify-content: center;
	gap: 2vw;
}

.end_msg {
	text-align: center;
	font-size: 1em;
	color: #0abd45;
}

.end_btn {
	width: 20vw;
	text-align: center;
	border: 0.25vw solid black;
	padding: 1vw 2vw;
	background-color: #deedff;
	font-weight: 900;
	cursor: pointer;
	transition: scale 0.5s;
}

.end_btn:hover {
	scale: 1.05;
}
</style>
